.Cast {
  margin-bottom: 50px;
}

.CastGalery {
  display: flex;
  flex-wrap: wrap;
  margin-right: -20px;
  padding-left: 0;
}

.CastCard {
  flex-basis: calc(25% - 20px);
  margin-left: 0;
  margin-right: 20px;
  margin-bottom: 30px;
  transform: scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.CastCard:hover {
  transform: scale(1.05);
  transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 5px 20px -5px #4050b5;
  border-radius: 5px;
}

.CastCard:nth-child(4n) {
  margin-right: 0;
  margin-bottom: 30px;
}

.CastCard:nth-last-child(-n + 4) {
  margin-bottom: 0;
}

.CastBox {
  min-width: 240px;
  height: 398px;
  overflow: hidden;
  cursor: pointer;
  border-radius: 5px;
}

.CastBoxImage {
  width: 100%;
  border-radius: 5px;
}

.CastName {
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.17;
  text-transform: uppercase;
  text-align: center;
  color: rgba(64, 80, 181, 0.9);
}

.CastCharacter {
  margin-top: 5px;
  margin-left: 5px;
  margin-bottom: 5px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.17;
  text-transform: uppercase;
  text-align: left;
  color: rgba(64, 80, 181, 0.9);
}

.CastCharacterInfo {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.17;
  text-transform: uppercase;
  color: #676767;
}

.CastNoTitle {
  margin-left: auto;
  margin-right: auto;
  font-size: 40px;
  text-align: center;
  color: #4050b5;
}
